import React from 'react';
import * as CustomCode from '../CustomCode.js';
import * as DcSSApi from '../apis/DcSSApi.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import * as Utils from '../utils';
import {
  ButtonSolid,
  ScreenContainer,
  Touchable,
  withTheme,
} from '@draftbit/ui';
import { useIsFocused } from '@react-navigation/native';
import { Image, StyleSheet, Text, View } from 'react-native';

const CompletaInicioSessionScreen = props => {
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;
  const setGlobalVariableValue = GlobalVariables.useSetValue();

  const showVars = () => {
    const platform = CustomCode.Platform.OS;
    console.log(`platform: ${platform}`);

    // const constants = CustomCode.ExpoConstants[CustomCode.Platform.OS];
    const constants = CustomCode.ExpoConstants;
    console.log(`constants: ${JSON.stringify(constants)}`);

    const isDevice = CustomCode.Platform.OS !== 'web' && constants.isDevice;
    console.log(`isDevice: ${isDevice}`);
  };

  const getNotificationToken = async () => {
    const isDevice =
      CustomCode.Device.isDevice && CustomCode.Platform.OS !== 'web'; // && CustomCode.ExpoConstants;

    if (isDevice && CustomCode.Device.isDevice) {
      const expoPushToken =
        await CustomCode.ExpoNotifications.getExpoPushTokenAsync({
          experienceId: '@digicard/dss',
        });

      console.log(`expoPushToken: $expoPushToken}`);

      return expoPushToken;
    } else return { data: '' };
  };

  const { theme } = props;
  const { navigation } = props;

  const dcSSEmployeeTokenPATCH = DcSSApi.useEmployeeTokenPATCH();

  const isFocused = useIsFocused();
  React.useEffect(() => {
    const handler = async () => {
      try {
        if (!isFocused) {
          return;
        }
        const expoToken = await getNotificationToken();
        setGlobalVariableValue({
          key: 'ExpoNotifToken',
          value: expoToken,
        });
        const tokenData = expoToken.data;
        await dcSSEmployeeTokenPATCH.mutateAsync({
          emp_id: Constants['my_emp_id'],
          emp_token: tokenData,
        });
        if (false) {
          return;
        }
        navigation.navigate('Main', { screen: 'InicioScreen' });
      } catch (err) {
        console.error(err);
      }
    };
    handler();
  }, [isFocused]);

  return (
    <ScreenContainer
      style={[styles.screen, { backgroundColor: theme.colors.background }]}
      scrollable={false}
      hasSafeArea={true}
    >
      <Utils.CustomCodeErrorBoundary>
        <CustomCode.DigicardScreenHeader {...props} left={{}} />
      </Utils.CustomCodeErrorBoundary>
      <View style={styles.Viewbf78ff24}>
        <Image style={styles.Image4a6f3b3d} resizeMode={'contain'} />
        <Text
          style={[styles.Text26752f1e, { color: theme.colors.strong }]}
          textBreakStrategy={'highQuality'}
          ellipsizeMode={'tail'}
          allowFontScaling={true}
        >
          {'Registrando Notificaciones...'}
        </Text>

        <Text
          style={[styles.Text684cd7ef, { color: theme.colors.strong }]}
          textBreakStrategy={'highQuality'}
          ellipsizeMode={'tail'}
          allowFontScaling={true}
        >
          {'Un momento por favor...'}
        </Text>
      </View>
    </ScreenContainer>
  );
};

