import React from 'react';
import * as CustomCode from '../CustomCode.js';
import * as DcSSApi from '../apis/DcSSApi.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import Images from '../config/Images';
import * as Utils from '../utils';
import {
  ButtonSolid,
  Container,
  ScreenContainer,
  TextField,
  Touchable,
  withTheme,
} from '@draftbit/ui';
import { useIsFocused } from '@react-navigation/native';
import * as WebBrowser from 'expo-web-browser';
import { Image, Platform, StyleSheet, Text, View } from 'react-native';
import { KeyboardAwareScrollView } from 'react-native-keyboard-aware-scroll-view';

const IniciarSesionScreen = props => {
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;
  const setGlobalVariableValue = GlobalVariables.useSetValue();

  // Checks if logged in
  const loggedIn = (authToken, refreshToken, whoami) => {
    return true;
  };

  const isEmpty = value => {
    const empty = !value || value?.length == 0;

    // console.log(`checking emptyness of '${value}': ${empty}`);

    return empty;
    // return true;
  };

  // Extract right error message from payload
  const extractError = body => {
    if (body.error) {
      if (body.error.details && body.error.details.length) {
        return `${body.error.details[0].path.substring(1)} ${body.error.details[0].message
          }`;
      }
      return body.error.message;
    }
    return '';
  };

  const validate = (value, regex, target_var) => {
    const invalid = !regex.test(value);
    console.log(`Value: ${value} Valid: ${!invalid}`);

    setVariable({ key: target_var, value: invalid });

    return invalid;
  };

  const logVar = aVar => {
    if (variables['logging']) console.log(`log value: ${JSON.stringify(aVar)}`);
    return aVar;
  };

  const passwordCheck = password => {
    return false; // En Login no doy pistas del formato de la password

    // De 8 a 100 cars, debe contener  1 mayúscula, 1 minúscula y 2 dígitos
    return validate(
      password,
      /^(?=.*[a-z])(?=.*[A-Z])(?=(.*\d){2,})[A-Za-z\d@$!%*?&]{8,100}$/,
      'SHOW_PASSWORD_ERROR'
    );
  };

  const breakIfEmpty = value => {
    const empty = isEmpty(value);

    if (empty) setLoading(false);

    console.log(`breakIfEmpty(${value}): ${empty}`);

    return empty;
  };

  const hideSplash = () => {
    CustomCode.hideSplash();
  };

  const skipIfLogged = (whoami, refreshToken) => {
    let homeScreen = 'InicioScreen';

    // console.log(`skipIfLogged(whoami: ${JSON.stringify(whoami)}, refreshToken: ${refreshToken})`);
    console.log(
      `skipIfLogged(whoami: ${whoami}, refreshToken: ${refreshToken})`
    );
    console.log(whoami);

    if (whoami?.error) {
      if (isEmpty(refreshToken)) {
        console.log(
          'whoami error. token expiró y no hay refresh token. Obligando a login'
        );
        setLoading(false);
        return true;
      } else {
        console.log(
          'whoami error. token expiró, las acciones subsiguientes trataran de renovarlo'
        );
        return false;
      }
    }
    // if(whoami?.emp_doc_number && refreshToken?.length) {  No tiene sentido preguntar, si no retornó error estoy conectado
    else console.log('whoami ok. Estoy conectado');

    setLoading(false);
    console.log(`skipIfLogged: navigating to ${homeScreen}`);
    props.navigation.navigate('Main', { screen: homeScreen });
    return true;
  };

  const resetStackNav = () => {
    if (props.navigation.getState) {
      const navState = props.navigation.getState();

      if (navState.routes.length > 1)
        props.navigation.reset({
          index: 0,
          routes: [{ name: 'Auth' }],
        });
    }
  };

  const emailCheck = email => {
    return validate(
      email,
      /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
      'SHOW_EMAIL_ERROR'
    );
  };
  // to use a global variable
  const variables = CustomCode.useValues();
  // to update or modify the value of a global variable
  const setVariable = CustomCode.useSetValue();
  const { theme } = props;
  const { navigation } = props;

  const isFocused = useIsFocused();
  React.useEffect(() => {
    const handler = async () => {
      try {
        if (!isFocused) {
          return;
        }
        hideSplash();
        setLoading(true);
        if (breakIfEmpty(Constants['AUTH_HEADER'])) {
          return;
        }
        const a_whoami = await DcSSApi.whoamiGET(Constants);
        const skip = skipIfLogged(a_whoami, Constants['REFRESH_TOKEN']);
        if (skip) {
          return;
        }
        const a_refresh = await DcSSApi.tokenRefreshPOST(Constants, {
          refreshToken: Constants['REFRESH_TOKEN'],
        });
        setLoading(false);
        const a_refresh_error = a_refresh.error;
        if (a_refresh_error) {
          return;
        }
        const a_accessToken = a_refresh.accessToken;
        const newAUTH_HEADER = setGlobalVariableValue({
          key: 'AUTH_HEADER',
          value: 'Bearer ' + a_accessToken,
        });
        console.log(newAUTH_HEADER);
        navigation.navigate('Main', { screen: 'InicioScreen' });
      } catch (err) {
        console.error(err);
      }
    };
    handler();
  }, [isFocused]);

  const [email, setEmail] = React.useState('');
  const [errorMessage, setErrorMessage] = React.useState('');
  const [loading, setLoading] = React.useState(false);
  const [password, setPassword] = React.useState('');
  const [whoami, setWhoami] = React.useState({});

  return (
    <ScreenContainer
      style={[styles.screen, { backgroundColor: theme.colors.background }]}
      hasSafeArea={true}
      scrollable={false}
    >
      <CustomCode.LoadingOverlay
        {...props}
        visible={loading}
        action={'connecting'}
      />

      <KeyboardAwareScrollView
        enableOnAndroid={false}
        enableAutomaticScroll={true}
        enableResetScrollToCoords={true}
        extraHeight={200}
      >
        <View style={styles.Viewe6a90082}>
          {/* app-logo */}
          <Image
            style={styles.Image5c7830a3}
            resizeMode={'contain'}
            source={Images.DigiSSTransp}
          />
          <View style={styles.View3831279f}>
            <Text
              style={[
                styles.Text1fc21409,
                {
                  color: theme.colors.strong,
                  textDecorationColor: theme.colors.primary,
                },
              ]}
            >
              {'Bienvenido !'}
            </Text>
          </View>
        </View>

        <View style={styles.View7675bd73}>
          <View style={styles.View5a3136e3}>
            <Text
              style={[
                theme.typography.subtitle2,
                { color: theme.colors.strong },
              ]}
            >
              {'Ingresá tu mail y contraseña'}
            </Text>
          </View>
        </View>

        <View>
          <Container
            style={styles.Container557e67a1}
            backgroundImageResizeMode={'cover'}
          >
            <View style={styles.View9f6786b9}>
              {/* Email */}
              <TextField
                onChangeText={newEmailValue => {
                  const email = newEmailValue;
                  try {
                    setEmail(email);
                  } catch (err) {
                    console.error(err);
                  }
                }}
                type={'solid'}
                value={email}
                label={'Email'}
                autoCapitalize={'none'}
                keyboardType={'email-address'}
                returnKeyType={'done'}
              />
              <>
                {!Constants['SHOW_EMAIL_ERROR'] ? null : (
                  <Text
                    style={[styles.Text8099cd07, { color: theme.colors.error }]}
                    numberOfLines={1}
                  >
                    {'Debe ser una dirección de email válida'}
                  </Text>
                )}
              </>
              {/* Password */}
              <TextField
                onChangeText={newPasswordValue => {
                  const password = newPasswordValue;
                  try {
                    setPassword(password);
                  } catch (err) {
                    console.error(err);
                  }
                }}
                style={styles.TextField70eb5f4e}
                placeholder={''}
                type={'solid'}
                label={'Contraseña'}
                value={password}
                returnKeyType={'done'}
                secureTextEntry={true}
              />
              <>
                {!Constants['SHOW_PASSWORD_ERROR'] ? null : (
                  <Text
                    style={[styles.Text8099cd07, { color: theme.colors.error }]}
                    numberOfLines={1}
                  >
                    {'De 8 a 100 cars,  1 mayúscula, 1 minúscula, 2 dígitos'}
                  </Text>
                )}
              </>
            </View>

            <View style={styles.Viewf07d5c49}>
              <>
                {isEmpty(errorMessage) ? null : (
                  <Text
                    style={[styles.Text109007a7, { color: theme.colors.error }]}
                  >
                    {errorMessage}
                  </Text>
                )}
              </>
            </View>
          </Container>
        </View>

        <View style={styles.View28bb13c2}>
          <Touchable
            onPress={() => {
              try {
                navigation.navigate('Auth', { screen: 'NuevoUsuarioScreen' });
              } catch (err) {
                console.error(err);
              }
            }}
          >
            <Text
              style={[styles.Text33bd86a8, { color: theme.colors.secondary }]}
            >
              {'No tenés cuenta ?  Creá una !'}
            </Text>
          </Touchable>
          <ButtonSolid
            onPress={() => {
              const handler = async () => {
                try {
                  loggedIn(undefined, undefined, undefined);
                  if (emailCheck(email)) {
                    return;
                  }
                  if (passwordCheck(password)) {
                    return;
                  }
                  setGlobalVariableValue({
                    key: 'loading',
                    value: true,
                  });
                  const loginResponse = await DcSSApi.loginPOST(Constants, {
                    email: email?.toLowerCase(),
                    password: password,
                  });
                  setGlobalVariableValue({
                    key: 'loading',
                    value: false,
                  });
                  console.log(loginResponse);
                  const errorMessage = extractError(loginResponse);
                  setErrorMessage(errorMessage);
                  if (errorMessage?.length) {
                    return;
                  }
                  const authToken = loginResponse.accessToken;
                  const AUTH_HEADER = setGlobalVariableValue({
                    key: 'AUTH_HEADER',
                    value: 'Bearer ' + authToken,
                  });
                  console.log(AUTH_HEADER);
                  if (!authToken) {
                    return;
                  }
                  const a_emp_id = loginResponse.emp_id;
                  const a_emp_com_id = loginResponse.emp_com_id;
                  setGlobalVariableValue({
                    key: 'my_emp_id',
                    value: parseInt(a_emp_id, 10),
                  });
                  setGlobalVariableValue({
                    key: 'my_emp_com_id',
                    value: a_emp_com_id,
                  });
                  const refreshToken = loginResponse.refreshToken;
                  const REFRESH_TOKEN = setGlobalVariableValue({
                    key: 'REFRESH_TOKEN',
                    value: refreshToken,
                  });
                  console.log(REFRESH_TOKEN);
                  navigation.navigate('Permissions', {
                    screen: 'BienvenidoScreen',
                  });
                } catch (err) {
                  console.error(err);
                }
              };
              handler();
            }}
            style={[
              styles.ButtonSolid01d1d684,
              {
                backgroundColor: theme.colors.primary,
                color: theme.colors.background,
              },
            ]}
            title={'Iniciar sesión'}
            loading={false}
            disabled={false}
          />
        </View>
        <>
          {!(Platform.OS === 'web') ? null : (
            <View style={styles.View0db48ba2}>
              <View style={styles.View39912261}>
                <Touchable
                  onPress={() => {
                    const handler = async () => {
                      try {
                        await WebBrowser.openBrowserAsync(
                          'https://play.google.com/store/apps/details?id=com.digicard.dss'
                        );
                      } catch (err) {
                        console.error(err);
                      }
                    };
                    handler();
                  }}
                >
                  {/* imgGoogle */}
                  <Image
                    style={styles.Image07a8b9e4}
                    source={Images.DisponibleEnGooglePlay}
                    resizeMode={'contain'}
                  />
                </Touchable>
                {/* qrGoogle */}
                <Image
                  style={styles.Image7bb45edf}
                  resizeMode={'cover'}
                  source={Images.QrGoogle}
                />
              </View>

              <View style={styles.View39912261}>
                <Touchable
                  onPress={() => {
                    const handler = async () => {
                      try {
                        await WebBrowser.openBrowserAsync(
                          'https://apps.apple.com/us/app/digicard-self-service/id1621603154'
                        );
                      } catch (err) {
                        console.error(err);
                      }
                    };
                    handler();
                  }}
                >
                  {/* imgApple */}
                  <Image
                    style={styles.Image07a8b9e4}
                    resizeMode={'contain'}
                    source={Images.DisponibleEnApple}
                  />
                </Touchable>
                {/* qrApple */}
                <Image
                  style={styles.Image8118445b}
                  resizeMode={'cover'}
                  source={Images.QrApple}
                />
              </View>
            </View>
          )}
        </>
      </KeyboardAwareScrollView>
    </ScreenContainer>
  );
};

