import React from 'react';
import * as CustomCode from '../CustomCode.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import Images from '../config/Images';
import * as Utils from '../utils';
import {
  ButtonSolid,
  ScreenContainer,
  Touchable,
  withTheme,
} from '@draftbit/ui';
import { useIsFocused } from '@react-navigation/native';
import { Image, StyleSheet, Text, View } from 'react-native';

const PermisoDeCamaraScreen = props => {
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;
  const setGlobalVariableValue = GlobalVariables.useSetValue();

  const askPermission = async () => {
    const { status } = await CustomCode.Camera.requestCameraPermissionsAsync();

    console.log(`status: ${status}`);

    return status === 'granted';
    // props.navigation.navigate(target);
  };

  const getPermission = async () => {
    if (CustomCode.Platform.OS === 'web') return true;

    const { status } = await CustomCode.Camera.getCameraPermissionsAsync();

    console.log(`status: ${status}`);

    return status === 'granted';
  };

  const { theme } = props;
  const { navigation } = props;

  const isFocused = useIsFocused();
  React.useEffect(() => {
    const handler = async () => {
      try {
        if (!isFocused) {
          return;
        }
        const hasPermission = await getPermission();
        if (!hasPermission) {
          return;
        }
        navigation.navigate('Permissions', {
          screen: 'CompletaInicioSessionScreen',
        });
      } catch (err) {
        console.error(err);
      }
    };
    handler();
  }, [isFocused]);

  return (
    <ScreenContainer
      style={[styles.screen, { backgroundColor: theme.colors.background }]}
      scrollable={false}
      hasSafeArea={true}
    >
      <Utils.CustomCodeErrorBoundary>
        <CustomCode.DigicardScreenHeader {...props} left={{}} />
      </Utils.CustomCodeErrorBoundary>
      <View style={styles.Viewff827ab7}>
        <Image
          style={styles.Image54764480}
          resizeMode={'contain'}
          source={Images.PermissionsCamera}
        />
        <Text
          style={[styles.Textb3ed6e8a, { color: theme.colors.strong }]}
          textBreakStrategy={'highQuality'}
          ellipsizeMode={'tail'}
          allowFontScaling={true}
        >
          {'Permitir Cámara'}
        </Text>

        <Text
          style={[styles.Text684cd7ef, { color: theme.colors.strong }]}
          textBreakStrategy={'highQuality'}
          ellipsizeMode={'tail'}
          allowFontScaling={true}
        >
          {
            'Lo necesitaremos para tomar fotos, acceder al carrete de la cámara o grabar videos.\n'
          }
        </Text>
      </View>

      <View style={styles.View2d677ab2}>
        <Touchable
          onPress={() => {
            try {
              navigation.navigate('Permissions', {
                screen: 'CompletaInicioSessionScreen',
              });
            } catch (err) {
              console.error(err);
            }
          }}
          style={styles.Touchable63a541e9}
        >
          <Text
            style={[
              theme.typography.button,
              styles.Textfad9fa52,
              { color: theme.colors.strong },
            ]}
            textBreakStrategy={'highQuality'}
            ellipsizeMode={'tail'}
            allowFontScaling={true}
          >
            {'Saltar'}
          </Text>
        </Touchable>
        <ButtonSolid
          onPress={() => {
            const handler = async () => {
              try {
                setGlobalVariableValue({
                  key: 'perms_got',
                  value: 1,
                });
                const granted = await askPermission();
                if (!granted) {
                  return;
                }
                navigation.navigate('Permissions', {
                  screen: 'CompletaInicioSessionScreen',
                });
              } catch (err) {
                console.error(err);
              }
            };
            handler();
          }}
          style={[
            styles.ButtonSolid5e96c084,
            {
              backgroundColor: theme.colors.primary,
              color: theme.colors.background,
            },
          ]}
          title={'Permitir Cámara'}
        />
      </View>
    </ScreenContainer>
  );
};

const styles = StyleSheet.create({
  ButtonSolid5e96c084: {
    borderRadius: 10,
    fontFamily: 'System',
    fontWeight: '700',
    marginLeft: '6%',
    marginRight: '6%',
    minHeight: 48,
    textAlign: 'center',
    width: '100%',
  },
  Image54764480: {
    height: 150,
    marginBottom: 16,
    overflow: 'hidden',
    width: 150,
  },
  Text684cd7ef: {
    alignSelf: 'center',
    fontSize: 16,
    lineHeight: 28,
    marginLeft: '3%',
    marginRight: '3%',
    textAlign: 'center',
  },
  Textb3ed6e8a: {
    alignSelf: 'center',
    fontFamily: 'System',
    fontWeight: '700',
    fontSize: 28,
  },
  Textfad9fa52: {
    opacity: 0.52,
    textAlign: 'center',
    textTransform: 'uppercase',
  },
  Touchable63a541e9: {
    marginBottom: '8%',
  },
  View2d677ab2: {
    alignContent: 'center',
    alignItems: 'center',
    justifyContent: 'flex-end',
    marginBottom: '10%',
    marginLeft: '6%',
    marginRight: '6%',
  },
  Viewff827ab7: {
    alignContent: 'center',
    alignItems: 'center',
    alignSelf: 'center',
    justifyContent: 'center',
  },
  screen: {
    justifyContent: 'space-between',
  },
});

export default withTheme(PermisoDeCamaraScreen);
