import React from 'react';
import * as CustomCode from '../CustomCode.js';
import * as DcSSApi from '../apis/DcSSApi.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import Images from '../config/Images';
import * as Utils from '../utils';
import {
  ButtonSolid,
  Container,
  IconButton,
  ScreenContainer,
  TextField,
  Touchable,
  withTheme,
} from '@draftbit/ui';
import { Image, StyleSheet, Text, View } from 'react-native';
import { KeyboardAwareScrollView } from 'react-native-keyboard-aware-scroll-view';

const NuevoUsuarioScreen = props => {
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;
  const setGlobalVariableValue = GlobalVariables.useSetValue();

  const passwordCopyCheck = (passwordCopy, password) => {
    const invalid = password !== passwordCopy;

    console.log(
      `PasswordMismatch: ${invalid}, password: [${password}[], passwordCopy: [${passwordCopy}]`
    );
    // setGlobalVariable({ key: 'SHOW_PASSWORD_MISMATCH_ERROR', value: invalid });
    setSHOW_PASSWORD_MISMATCH_ERROR(invalid);

    return invalid;
  };

  const cuilCheck = cuil => {
    const chkCuitChecksum = sCUIT => {
      var aMult = '5432765432';
      var aMult = aMult.split('');
      var rawCUIT = sCUIT.replace(/-/g, '');

      // console.log('CUIT: ', sCUIT);
      // console.log('CUIT: ', rawCUIT);

      if (rawCUIT && rawCUIT.length == 11) {
        var aCUIT = rawCUIT.split('');
        var iResult = 0;
        for (var i = 0; i <= 9; i++) {
          iResult += aCUIT[i] * aMult[i];
        }
        iResult = iResult % 11;
        iResult = 11 - iResult;

        if (iResult == 11) iResult = 0;
        if (iResult == 10) iResult = 9;

        console.log(`vdigit: ${iResult}`);

        if (iResult == aCUIT[10]) return true;
      }
      return false;
    };

    const regex = /^(20|23|24|27|30|33|34)([0-9]{9}|-[0-9]{8}-[0-9]{1})$/;
    const invalid = !regex.test(cuil) || !chkCuitChecksum(cuil);
    console.log(`CUIL Value: ${cuil} Valid: ${!invalid}`);

    // setGlobalVariable({ key: 'SHOW_CUIL_ERROR', value: invalid });
    setSHOW_CUIL_ERROR(invalid);

    return invalid;
  };

  const validate = (value, regex, target_var) => {
    const invalid = !regex.test(value);
    console.log(`Value: ${value} Valid: ${!invalid}`);

    // setGlobalVariable({ key: target_var, value: invalid });
    target_var(invalid);

    return invalid;
  };

  const emailCheck = email => {
    return validate(
      email,
      /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
      setSHOW_EMAIL_ERROR
    );
  };

  const passwordCheck = password => {
    // De 8 a 100 cars, debe contener  1 mayúscula, 1 minúscula y 2 dígitos
    return validate(
      password,
      /^(?=.*[a-z])(?=.*[A-Z])(?=(.*\d){2,})[A-Za-z\d@$!%*?&]{8,100}$/,
      setSHOW_PASSWORD_ERROR
    );
  };

  const extractError = body => {
    if (body.error) {
      if (body.error.details && body.error.details.length) {
        return `${body.error.details[0].path.substring(1)} ${
          body.error.details[0].message
        }`;
      }
      return body.error.message;
    }
    return '';
  };
  // ======================== Inline Code ========================
  // to use a global variable inside the component logic
  const globalVariables = CustomCode.useValues();
  // to update or modify the value of a global variable
  const setGlobalVariable = CustomCode.useSetValue();
  // =================== End  Inline Code ========================
  const { theme } = props;
  const { navigation } = props;

  const [SHOW_CUIL_ERROR, setSHOW_CUIL_ERROR] = React.useState(false);
  const [SHOW_EMAIL_ERROR, setSHOW_EMAIL_ERROR] = React.useState(false);
  const [SHOW_PASSWORD_ERROR, setSHOW_PASSWORD_ERROR] = React.useState(false);
  const [SHOW_PASSWORD_MISMATCH_ERROR, setSHOW_PASSWORD_MISMATCH_ERROR] =
    React.useState(false);
  const [cuil, setCuil] = React.useState('');
  const [email, setEmail] = React.useState('');
  const [errorMessage, setErrorMessage] = React.useState('');
  const [loading, setLoading] = React.useState(false);
  const [password, setPassword] = React.useState('');
  const [passwordCopy, setPasswordCopy] = React.useState('');
  const [showPasswordHint, setShowPasswordHint] = React.useState(false);

  return (
    <ScreenContainer
      style={[styles.screen, { backgroundColor: theme.colors.background }]}
      hasSafeArea={true}
      scrollable={false}
    >
      <CustomCode.LoadingOverlay
        {...props}
        visible={loading}
        action={'connecting'}
      />
      {/* PasswordHintView */}
      <>
        {!showPasswordHint ? null : (
          <View
            style={[
              styles.View70389204,
              { backgroundColor: theme.colors.tooltip },
            ]}
          >
            <Text style={[styles.Text98220b42, { color: theme.colors.strong }]}>
              {
                'Longitud: 8 a 100 cars\nAl menos una letra mayúscula\nAl menos una letra minúscula\nAl menos 2 dígitos\nNo debe contener espacios'
              }
            </Text>
          </View>
        )}
      </>
      <KeyboardAwareScrollView
        style={styles.KeyboardAwareScrollView2200bac7}
        enableOnAndroid={true}
        enableAutomaticScroll={true}
        enableResetScrollToCoords={true}
        extraHeight={200}
      >
        <View style={styles.Viewde75f2a8}>
          {/* app-logo */}
          <Image
            style={styles.Image96e99761}
            resizeMode={'contain'}
            source={Images.DigiSSTransp}
          />
          <View style={styles.View81311079}>
            <Text
              style={[
                styles.Text3184c160,
                {
                  color: theme.colors.strong,
                  textDecorationColor: theme.colors.primary,
                },
              ]}
            >
              {'Bienvenido !'}
            </Text>
          </View>
        </View>

        <View style={styles.View959c72fe}>
          <View style={styles.View3a1abe40}>
            <Text
              style={[
                theme.typography.subtitle2,
                { color: theme.colors.strong },
              ]}
            >
              {'Para crear tu cuenta ingresá estos datos '}
            </Text>
          </View>
        </View>

        <Container
          style={styles.Containerc11009a4}
          backgroundImageResizeMode={'cover'}
        >
          <View style={styles.View9409b140}>
            <View>
              {/* Cuil */}
              <TextField
                onChangeText={newCuilValue => {
                  const cuil = newCuilValue;
                  try {
                    setCuil(cuil);
                  } catch (err) {
                    console.error(err);
                  }
                }}
                style={styles.TextFieldb5743ecd}
                label={'CUIL'}
                type={'solid'}
                underlineColor={theme.colors.light}
                value={cuil}
                editable={true}
              />
              <>
                {!SHOW_CUIL_ERROR ? null : (
                  <Text
                    style={[styles.Text8099cd07, { color: theme.colors.error }]}
                    numberOfLines={1}
                  >
                    {'El número de CUIL es incorrecto'}
                  </Text>
                )}
              </>
            </View>

            <View>
              {/* Email */}
              <TextField
                onChangeText={newEmailValue => {
                  const email = newEmailValue;
                  try {
                    setEmail(email);
                  } catch (err) {
                    console.error(err);
                  }
                }}
                style={styles.TextFieldb5743ecd}
                label={'e-mail'}
                type={'solid'}
                underlineColor={theme.colors.light}
                value={email}
                keyboardType={'email-address'}
                editable={true}
              />
              <>
                {!SHOW_EMAIL_ERROR ? null : (
                  <Text
                    style={[styles.Text8099cd07, { color: theme.colors.error }]}
                    numberOfLines={1}
                  >
                    {'Debe ser una dirección de email válida'}
                  </Text>
                )}
              </>
            </View>

            <View>
              <View>
                {/* Password */}
                <TextField
                  onChangeText={newPasswordValue => {
                    const password = newPasswordValue;
                    try {
                      setPassword(password);
                    } catch (err) {
                      console.error(err);
                    }
                  }}
                  style={styles.TextFieldb5743ecd}
                  label={'Contraseña'}
                  type={'solid'}
                  underlineColor={theme.colors.light}
                  value={password}
                  keyboardType={'visible-password'}
                  editable={true}
                />
                <View style={styles.Viewdbeefdac}>
                  <>
                    {!false ? null : (
                      <IconButton
                        onPress={() => {
                          try {
                            setGlobalVariableValue({
                              key: 'showPasswordHint',
                              value: '',
                            });
                          } catch (err) {
                            console.error(err);
                          }
                        }}
                        icon={'Ionicons/ios-help-circle-outline'}
                        size={24}
                      />
                    )}
                  </>
                </View>
              </View>
            </View>
            <>
              {!SHOW_PASSWORD_ERROR ? null : (
                <Text
                  style={[styles.Text8099cd07, { color: theme.colors.error }]}
                  numberOfLines={2}
                >
                  {'De 8 a 100 cars,  1 mayúscula, 1 minúscula, 2 dígitos'}
                </Text>
              )}
            </>
            <View>
              {/* PasswordCopy */}
              <TextField
                onChangeText={newPasswordCopyValue => {
                  const passwordCopy = newPasswordCopyValue;
                  try {
                    setPasswordCopy(passwordCopy);
                  } catch (err) {
                    console.error(err);
                  }
                }}
                style={styles.TextField9d189bec}
                label={'Repetir contraseña'}
                type={'solid'}
                underlineColor={theme.colors.light}
                value={passwordCopy}
                keyboardType={'visible-password'}
                editable={true}
              />
              <>
                {!SHOW_PASSWORD_MISMATCH_ERROR ? null : (
                  <Text
                    style={[styles.Text8099cd07, { color: theme.colors.error }]}
                    numberOfLines={1}
                  >
                    {'Las contraseñas no coinciden'}
                  </Text>
                )}
              </>
            </View>
          </View>

          <View style={styles.Viewa0ada30a}>
            <>
              {!errorMessage?.length ? null : (
                <Text
                  style={[styles.Text4c4d3077, { color: theme.colors.error }]}
                >
                  {errorMessage}
                </Text>
              )}
            </>
          </View>
        </Container>

        <View style={styles.View82343076}>
          <Touchable
            onPress={() => {
              try {
                navigation.navigate('Auth', { screen: 'IniciarSesionScreen' });
              } catch (err) {
                console.error(err);
              }
            }}
          >
            <Text
              style={[styles.Texta9f0f143, { color: theme.colors.secondary }]}
            >
              {'Ya tenés cuenta,? Iniciá sesión!'}
            </Text>
          </Touchable>
          <ButtonSolid
            onPress={() => {
              const handler = async () => {
                try {
                  if (cuilCheck(cuil)) {
                    return;
                  }
                  if (emailCheck(email)) {
                    return;
                  }
                  if (passwordCheck(password)) {
                    return;
                  }
                  const passwordMismatch = passwordCopyCheck(
                    passwordCopy,
                    password
                  );
                  if (passwordMismatch) {
                    return;
                  }
                  setLoading(true);
                  const signupResponse = await DcSSApi.signupPOST(Constants, {
                    cuil: cuil,
                  });
                  setLoading(false);
                  console.log(signupResponse);
                  const errorMessage = extractError(signupResponse);
                  setErrorMessage(errorMessage);
                  if (errorMessage?.length) {
                    return;
                  }
                  const emailVerified = signupResponse.emailVerified;
                  console.log(emailVerified);
                  if (!emailVerified) {
                    return;
                  }
                  const username = signupResponse.username;
                  setLoading(true);
                  const loginResponse = await DcSSApi.loginPOST(Constants, {
                    email: username?.toLowerCase(),
                    password: password,
                  });
                  setLoading(false);
                  const loginError = extractError(loginResponse);
                  setErrorMessage(loginError);
                  if (loginError?.length) {
                    return;
                  }
                  const authToken = loginResponse.accessToken;
                  const AUTH_HEADER = setGlobalVariableValue({
                    key: 'AUTH_HEADER',
                    value: 'Bearer ' + authToken,
                  });
                  console.log(AUTH_HEADER);
                  const refreshToken = loginResponse.refreshToken;
                  const REFRESH_TOKEN = setGlobalVariableValue({
                    key: 'REFRESH_TOKEN',
                    value: refreshToken,
                  });
                  console.log(REFRESH_TOKEN);
                  const a_emp_id = loginResponse.emp_id;
                  setGlobalVariableValue({
                    key: 'my_emp_id',
                    value: a_emp_id,
                  });
                  const a_emp_com_id = loginResponse.emp_com_id;
                  setGlobalVariableValue({
                    key: 'my_emp_com_id',
                    value: a_emp_com_id,
                  });
                  navigation.navigate('Permissions', {
                    screen: 'BienvenidoScreen',
                  });
                } catch (err) {
                  console.error(err);
                }
              };
              handler();
            }}
            style={[
              styles.ButtonSolidec57d64e,
              {
                backgroundColor: theme.colors.primary,
                color: theme.colors.background,
              },
            ]}
            title={'Crear nuevo usuario'}
          />
        </View>
      </KeyboardAwareScrollView>
    </ScreenContainer>
  );
};

