const ConsultaComedorScreen = props => {
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;

  const InitializeCurrentDate = () => {
    return CustomCode.InitializeCurrentDate(Constants);
  };

  const setShow = what => {
    setShowing(what);

    calcDateFromByOption(what);

    console.log(`setShow(${what})`);
  };

  const extractDate = sDate => {
    return CustomCode.FormatIsoString(sDate, 'DD/MM');
  };

  const log = value => {
    if (variables['logging'])
      console.log(`log value: ${JSON.stringify(value)}`);
    return value;
  };

  const extractTime = sDate => {
    return CustomCode.FormatIsoString(sDate, 'HH:mm');
  };

  const dateAdd = (aDate, days) => {
    const newDate = new Date(aDate.getTime() + days * 1000 * 60 * 60 * 24);
    if (variables['logging'])
      console.log(`dateSAdd(${aDate}, ${days}) => ${newDate}`);
    return newDate;
  };

  const conceptName = con_id => {
    const found = screenData.find(e => e.con_id == con_id);

    return found?.con_name;
  };

  const calcDateFromByOption = showOption => {
    let rslt = new Date(dateTo);

    console.log(`calcDateFromByOption(${showOption}): ${rslt}`);
    switch (showOption) {
      case 0:
        break;

      case 1:
        /// To previous monday
        let offset = rslt.getDay() - 1;
        if (offset < 0) offset += 7;
        rslt = dateAdd(rslt, -offset);
        break;

      case 2:
        /// To start of month
        rslt.setDate(1);
        break;

      case 3:
        /// To start of year
        rslt.setMonth(0, 1);
        break;
    }

    setDateFrom(rslt);
    console.log(`calcDateFromByOption(${showOption}): ${rslt}`);
  };

  const DateToUtcString = aDate => {
    if (aDate) {
      // if(variables['logging']) console.log(
      // `Date2UTCString(${aDate}), type: ${typeof aDate}, ISO: ${aDate.toISOString()}, Local: ${aDate.toLocaleDateString()}`
      // );

      aDate.setHours(0);

      const UTCString = aDate.toISOString().split('T')[0];
      // if(variables['logging']) console.log(`UTCString: ${UTCString}`);
      return UTCString;
    }
    return '';
  };

  const calcDateFrom = aDateTo => {
    let rslt = aDateTo;

    if (!rslt) rslt = dateTo;

    console.log(`calcDateFrom(${currentDate}): ${rslt}`);
    switch (showing) {
      case 1:
        break;

      case 2:
        /// To previous monday
        let offset = rslt.getDay() - 1;
        if (offset < 0) offset += 7;
        rslt = dateAdd(rslt, -offset);
        break;

      case 3:
        /// To start of month
        rslt.setDate(1);
        break;

      case 4:
        /// To start of year
        rslt.setMonth(0, 1);
        break;
    }

    setDateFrom(rslt);
    console.log(`calcDateFrom(${currentDate}): ${rslt}`);
    return rslt;
  };
  // to use a global variable
  const variables = CustomCode.useValues();
  // to update or modify the value of a global variable
  const setVariable = CustomCode.useSetValue();

  const DropDownHolder = CustomCode.DropDownHolder;

  // const Nophoto = Images.Nophoto;
  // ["Ninguno","Vehiculo","Moto","Bici","Publico"]
  const { theme } = props;

  const {
    loading: screenLoading,
    data: screenData,
    error: screenError,
  } = DcSSApi.useConceptsByEmpGET({ emp_id: Constants['my_emp_id'] });

  const isFocused = useIsFocused();
  React.useEffect(() => {
    try {
      if (!isFocused) {
        return;
      }
      setDateTo(log(currentDate));
      setDateFrom(calcDateFrom(currentDate));
    } catch (err) {
      console.error(err);
    }
  }, [isFocused]);

  const [currentDate, setCurrentDate] = React.useState(
    InitializeCurrentDate("''")
  );
  const [dateFrom, setDateFrom] = React.useState(new Date());
  const [dateTo, setDateTo] = React.useState(new Date());
  const [daySheet, setDaySheet] = React.useState([]);
  const [screenName, setScreenName] = React.useState('Comedor');
  const [showing, setShowing] = React.useState(0);
  if (screenLoading) {
    return (
      <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
        <ActivityIndicator />
      </View>
    );
  }

  if (screenError) {
    return null;
  }

  if (!screenData) {
    return null;
  }

  return (
    <ScreenContainer
      style={styles.screen}
      scrollable={false}
      hasSafeArea={true}
    >
      <Utils.CustomCodeErrorBoundary>
        <CustomCode.DigicardScreenHeader
          {...props}
          right={{
            icon: 'Ionicons/home-outline',
            nav: 'Main',
            screen: 'InicioScreen',
          }}
          title={screenName}
        />
      </Utils.CustomCodeErrorBoundary>
      {/* DatePickerHeaderView */}
      <View style={styles.Viewd29d438e}>
        <CustomCode.rnSegmentedControlTab
          values={['Hoy', 'Semana', 'Mes', 'Año']}
          onTabPress={index => setShow(index)}
          selectedIndex={showing}
        />
      </View>

      <View style={styles.View9409b140}>
        <DcSSApi.FetchComedor2ByEmpGET
          date_from={DateToUtcString(dateFrom)}
          emp_id={Constants['my_emp_id']}
        >
          {({ loading, error, data, refetchComedor2ByEmp }) => {
            const fetchData = data;
            if (!fetchData || loading) {
              return <ActivityIndicator />;
            }

            if (error) {
              return (
                <Text style={{ textAlign: 'center' }}>
                  There was a problem fetching this data
                </Text>
              );
            }

            return (
              <>
                {/* SectionHeaderView */}
                <View style={styles.View81b7299e}>
                  {/* TableHeaderView */}
                  <View
                    style={[
                      styles.View7adcf214,
                      { backgroundColor: theme.colors.light },
                    ]}
                  >
                    <View style={styles.View83b5a101}>
                      <Text
                        style={[
                          styles.Text057c17d2,
                          { color: theme.colors.strong },
                        ]}
                      >
                        {'Fecha'}
                      </Text>
                    </View>

                    <View style={styles.View51e53994}>
                      <Text
                        style={[
                          styles.Textcb695d43,
                          { color: theme.colors.strong },
                        ]}
                      >
                        {'#'}
                      </Text>
                    </View>

                    <View style={styles.View36f814a7}>
                      <Text
                        style={[
                          styles.Text057c17d2,
                          { color: theme.colors.strong },
                        ]}
                      >
                        {'Menú'}
                      </Text>
                    </View>

                    <View style={styles.Viewf6209bd6}>
                      <Text
                        style={[
                          styles.Text5eae6685,
                          { color: theme.colors.strong },
                        ]}
                      >
                        {'Terminal'}
                      </Text>
                    </View>
                  </View>
                </View>
                <FlatList
                  data={fetchData}
                  listKey={'ZRb5NsVy'}
                  keyExtractor={listData =>
                    listData?.id || listData?.uuid || JSON.stringify(listData)
                  }
                  renderItem={({ item }) => {
                    const listData = item;
                    return (
                      <View
                        style={[
                          styles.View4fe20a6e,
                          { backgroundColor: theme.colors.verylight },
                        ]}
                      >
                        <View style={styles.Viewa5111455}>
                          <Text style={{ color: theme.colors.strong }}>
                            {extractDate(listData?.tio_date)}
                          </Text>
                        </View>

                        <View style={styles.View51e53994}>
                          <Text
                            style={[
                              styles.Texta7c3dbcd,
                              { color: theme.colors.strong },
                            ]}
                          >
                            {listData?.tio_amount}
                          </Text>
                        </View>

                        <View style={styles.View36f814a7}>
                          <Text
                            style={[
                              styles.Textec614b6f,
                              { color: theme.colors.strong },
                            ]}
                            numberOfLines={1}
                          >
                            {listData?.tio_menu}
                          </Text>
                        </View>

                        <View style={styles.View1c9a517f}>
                          <Text
                            style={[
                              styles.Textec614b6f,
                              { color: theme.colors.strong },
                            ]}
                            numberOfLines={1}
                          >
                            {listData?.tio_terminal}
                          </Text>
                        </View>
                      </View>
                    );
                  }}
                  contentContainerStyle={styles.FlatListb9147c3aContent}
                />
              </>
            );
          }}
        </DcSSApi.FetchComedor2ByEmpGET>
      </View>
    </ScreenContainer>
  );
};

