import React from 'react';
import * as CustomCode from '../CustomCode.js';
import * as DcSSApi from '../apis/DcSSApi.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import Images from '../config/Images';
import * as Utils from '../utils';
import {
  CircleImage,
  Container,
  Divider,
  Icon,
  ScreenContainer,
  Touchable,
  withTheme,
} from '@draftbit/ui';
import { useIsFocused } from '@react-navigation/native';
import {
  ActivityIndicator,
  Image,
  ScrollView,
  StyleSheet,
  Text,
  View,
} from 'react-native';

const InicioScreen = props => {
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;
  const setGlobalVariableValue = GlobalVariables.useSetValue();

  const checkPermsError = perms => {
    console.log(`checkPermsError: ${JSON.stringify(perms)}`);
    console.log(`perms error: ${perms.error}`);

    if (perms.error) return [];

    return perms;
  };

  const menuItems = () => {
    return [
      {
        name: 'Consultas',
        icon: 'MaterialCommunityIcons/frequently-asked-questions',
        background: Images.UndrawSearch01,
        target: 'ConsultasScreen',
        enabled: true,
        visible: true,
        permission: 'Inicio:Consultas',
        ifNoPerm: {
          enabled: true,
          visible: true,
        },
      },
      {
        name: 'Solicitudes',
        icon: 'FontAwesome/pencil-square-o',
        background: Images.UndrawRequest01,
        target: 'SolicitudesScreen',
        enabled: true,
        visible: true,
        permission: 'Inicio:Solicitudes',
        ifNoPerm: {
          enabled: true,
          visible: true,
        },
      },
      {
        name: 'Fichada Móvil',
        icon: 'MaterialCommunityIcons/account-clock',
        background: Images.UndrawPunch01,
        target: 'ConsultasScreen',
        enabled: true,
        visible: true,
        permission: 'Inicio:FichadaMovil',
        ifNoPerm: {
          visible: false,
          enabled: false,
        },
      },
      {
        name: 'Aprobaciones',
        icon: 'FontAwesome/check-square-o',
        background: Images.UndrawChecklist,
        target: 'AprobacionesScreen',
        enabled: true,
        visible: true,
        permission: 'Inicio:Aprobaciones',
        ifNoPerm: {
          enabled: false,
          visible: false,
        },
      },
    ];
  };

  const initVars = () => {
    console.log(`Platform: ${JSON.stringify(CustomCode.Platform)}`);
    switch (CustomCode.Platform.OS) {
      case 'web':
        setModalTopPadding(6);
        setModalLeftPadding(12);
        break;

      case 'ios':
        setModalTopPadding(30);
        setModalLeftPadding(12);
        break;

      case 'android':
        setModalTopPadding(6);
        setModalLeftPadding(10);
        break;
    }
  };

  const resetStackNav = () => {
    if (props.navigation.getState) {
      const navState = props.navigation.getState();

      if (navState.routes.length > 1)
        props.navigation.reset({
          index: 0,
          routes: [{ name: 'Main' }],
        });
    }
  };
  // ==============  INLINE API REST HOOKS ====================
  //
  // to use a global variable
  const variables = CustomCode.useValues();
  // to update or modify the value of a global variable
  const setVariable = CustomCode.useSetValue();

  /*
const {
loading: employeeLoading,
data: employeeData,
error: employeeError,
} = CustomCode.useEmployeeFilteredGET({ 
emp_id: Constants['my_emp_id'],     
body: {
  "fields": {
    "emp_id": true,
    "emp_first_name": true,
    "emp_com_id": true,
  }
}
});

setVariable('firstName' , employeeData?.emp_first_name + ' (de API)');
console.log(`useEmployeeFilteredGET. data: ${JSON.stringify(employeeData)}`);

//
// ==============  INLINE API REST HOOKS END ================
*/
  const { theme } = props;
  const { navigation } = props;

  const {
    loading: screenLoading,
    data: screenData,
    error: screenError,
  } = DcSSApi.useEmployeeBasicGET({ emp_id: Constants['my_emp_id'] });

  const isFocused = useIsFocused();
  React.useEffect(() => {
    const handler = async () => {
      try {
        if (!isFocused) {
          return;
        }
        initVars();
        resetStackNav();
        const emp_photo_rslt = await DcSSApi.employeePhotoGET(Constants, {
          emp_id: Constants['my_emp_id'],
        });
        const a_emp_photo = emp_photo_rslt.emp_photo;
        setGlobalVariableValue({
          key: 'emp_photo',
          value: a_emp_photo,
        });
        const permissions = await DcSSApi.permissionsGET(Constants, {
          emp_id: Constants['my_emp_id'],
        });
        console.log(permissions);
        setGlobalVariableValue({
          key: 'AppPermissions',
          value: checkPermsError(permissions),
        });
      } catch (err) {
        console.error(err);
      }
    };
    handler();
  }, [isFocused]);
  React.useEffect(() => {
    const handler = async () => {
      try {
        const company_rslt = await DcSSApi.companyGET(Constants, {
          com_id: Constants['my_emp_com_id'],
        });
        const a_company_name = company_rslt.com_description;
        setGlobalVariableValue({
          key: 'company_name',
          value: a_company_name,
        });
      } catch (err) {
        console.error(err);
      }
    };
    handler();
  }, [screenData]);
  const [modalLeftPadding, setModalLeftPadding] = React.useState(22);
  const [modalTopPadding, setModalTopPadding] = React.useState(6);
  const [showDrawer, setShowDrawer] = React.useState(false);
  const [showMenu, setShowMenu] = React.useState(false);
  if (screenLoading) {
    return (
      <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
        <ActivityIndicator />
      </View>
    );
  }

  if (screenError) {
    return null;
  }

  if (!screenData) {
    return null;
  }

  return (
    <ScreenContainer
      scrollable={false}
      hasSafeArea={true}
      hasTopSafeArea={true}
    >
      <CustomCode.DigicardScreenHeader
        {...props}
        left={{ icon: 'Feather/menu', onPress: () => setShowDrawer(true) }}
        right={{
          icon: 'Feather/bell',
          nav: 'Notifications',
          screen: 'NotificacionesScreen',
        }}
      />
      <View style={styles.View95d26ca3}>
        <View>
          <View style={styles.Viewbecf5b64}>
            <Touchable
              onPress={() => {
                try {
                  setShowMenu(!showMenu);
                } catch (err) {
                  console.error(err);
                }
              }}
            >
              <Text
                style={[styles.Textcabcb76c, { color: theme.colors.secondary }]}
              >
                {Constants['company_name']}
              </Text>
            </Touchable>
          </View>

          <View style={styles.View01582e76}>
            <View style={styles.View3f26a659}>
              <Text
                style={[styles.Textdeec852b, { color: theme.colors.strong }]}
              >
                {'Hola '}
                {screenData?.emp_first_name}
                {'!'}
              </Text>

              <Text
                style={[styles.Texte39f30c7, { color: theme.colors.primary }]}
              >
                {'Bienvenido/a a Digicard Self Service'}
              </Text>
            </View>
            <CircleImage
              source={{ uri: `${Constants['emp_photo']}` }}
              size={80}
            />
          </View>
          <View />
        </View>

        <View style={styles.Viewccb3e7f1}>
          <Icon
            style={styles.Icon8bd683a8}
            name={'MaterialCommunityIcons/phone-in-talk'}
            size={24}
            color={theme.colors.optionSelected}
          />
          <Text
            style={[
              styles.Textbad588b8,
              { color: theme.colors.optionSelected },
            ]}
          >
            {'Tu linea directa con RRHH'}
          </Text>
        </View>
      </View>

      <View style={styles.View2f8f92be}>
        <Utils.CustomCodeErrorBoundary>
          {CustomCode.renderMenuItems(
            menuItems(),
            Constants['AppPermissions'],
            props
          )}
        </Utils.CustomCodeErrorBoundary>
      </View>
      {/* OptionsModal */}
      <View removeClippedSubviews={false}>
        <CustomCode.SafeAreaView>
          <CustomCode.rnModal
            isVisible={showDrawer}
            animationIn={'slideInLeft'}
            animationOut={'slideOutLeft'}
            backdropColor={'white'}
            backdropOpacity={0.1}
            onBackdropPress={() => setShowDrawer(false)}
            onSwipeComplete={() => setShowDrawer(false)}
            swipeDirection={'left'}
            swipeThreshold={50}
            style={{ margin: 0 }}
            hideModalContentWhileAnimating={true}
          >
            <Container
              style={{
                flex: 1,
                width: '85%',
                alignSelf: 'flex-start',
                opacity: 0.95,
                paddingTop: modalTopPadding,
                paddingLeft: modalLeftPadding,
                backgroundColor: theme.colors.optionmenu,
                borderRadius: 0,
                borderTopRightRadius: 16,
                borderBottomRightRadius: 16,
              }}
              backgroundImageResizeMode={'cover'}
            >
              <Container
                style={[styles.Container3768a324, { borderRadius: 1 }]}
                backgroundImageResizeMode={'cover'}
              >
                <View style={{ borderRadius: 23 }}>
                  <Touchable
                    onPress={() => {
                      try {
                        setShowDrawer(false);
                      } catch (err) {
                        console.error(err);
                      }
                    }}
                    style={styles.Touchableedd61be4}
                  >
                    <Icon
                      name={'MaterialCommunityIcons/backburger'}
                      size={32}
                      color={theme.colors.background}
                    />
                  </Touchable>

                  <Touchable
                    onPress={() => {
                      try {
                        setShowDrawer(false);
                        navigation.navigate('Main', { screen: 'AboutScreen' });
                      } catch (err) {
                        console.error(err);
                      }
                    }}
                    style={styles.Touchable3816561a}
                  >
                    <View style={styles.Viewabea5cda}>
                      <Icon
                        style={styles.Icon6dc4da3d}
                        size={24}
                        color={theme.colors.background}
                        name={'AntDesign/infocirlceo'}
                      />
                      <Text
                        style={[
                          styles.Textaf6edb60,
                          { color: theme.colors.background },
                        ]}
                      >
                        {'Acerca de...'}
                      </Text>
                    </View>
                  </Touchable>

                  <Touchable
                    onPress={() => {
                      try {
                        setGlobalVariableValue({
                          key: 'AUTH_HEADER',
                          value: '',
                        });
                        setShowDrawer(false);
                        navigation.navigate('Auth', {
                          screen: 'IniciarSesionScreen',
                        });
                      } catch (err) {
                        console.error(err);
                      }
                    }}
                    style={styles.Touchableedd61be4}
                  >
                    <View style={styles.Viewabea5cda}>
                      <Icon
                        style={styles.Icon6dc4da3d}
                        name={'SimpleLineIcons/logout'}
                        size={24}
                        color={theme.colors.background}
                      />
                      <Text
                        style={[
                          styles.Textaf6edb60,
                          { color: theme.colors.background },
                        ]}
                      >
                        {'Cerrar sesión'}
                      </Text>
                    </View>
                  </Touchable>
                </View>
              </Container>
            </Container>
          </CustomCode.rnModal>
        </CustomCode.SafeAreaView>
      </View>
    </ScreenContainer>
  );
};

