import React from 'react';
import * as CustomCode from '../CustomCode.js';
import * as DcSSApi from '../apis/DcSSApi.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import Images from '../config/Images';
import * as Utils from '../utils';
import {
  ButtonSolid,
  DatePicker,
  ScreenContainer,
  withTheme,
} from '@draftbit/ui';
import { useIsFocused } from '@react-navigation/native';
import { Image, StyleSheet, Text, View } from 'react-native';

const SolicitudVacacionesScreen = props => {
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;

  const now = () => {
    return new Date().toISOString();
  };

  const postResultToast = fetchResult => {
    return CustomCode.PostResultToast(
      fetchResult,
      screenName,
      'back',
      '',
      2000,
      props
    );
  };

  const validate = () => {
    setDateFromError(undefined);
    setDateToError(undefined);

    // console.log(`validate: dateFrom: ${dateFrom}, dateTo: ${dateTo}`);

    const minDate = new Date();
    minDate.setDate(1 + minDate.getDate());
    minDate.setHours(0, 0, 0, 0);

    if (dateFrom >= minDate)
      if (dateTo >= dateFrom) return false;
      else setDateToError("'Hasta' debe ser posterior a 'Desde'");
    else setDateFromError('No puede comenzar antes de mañana');

    return true;
  };

  const initScreenVars = () => {
    setDateFrom(undefined);
    setDateTo(undefined);
    setDateFromError(undefined);
    setDateToError(undefined);
    // setConcept(undefined);
    // setReason(undefined);
  };

  // Convierte un Date a un string YYYY-MM-DD
  const Date2IsoDateString = aDate => {
    return CustomCode.Date2IsoDateString(aDate);
  };
  // to use a global variable
  const variables = CustomCode.useValues();
  // to update or modify the value of a global variable
  const setVariable = CustomCode.useSetValue();

  const DropDownHolder = CustomCode.DropDownHolder;

  // const Nophoto = Images.Nophoto;
  // ["Ninguno","Vehiculo","Moto","Bici","Publico"]

  // setVariable({ key: 'loading', value: true });
  const { theme } = props;

  const dcSSRequestVacPOST = DcSSApi.useRequestVacPOST();

  const isFocused = useIsFocused();
  React.useEffect(() => {
    try {
      if (!isFocused) {
        return;
      }
      initScreenVars();
      setDateFrom(props.route?.params?.dateFrom ?? '');
      setDateTo(props.route?.params?.dateTo ?? '');
    } catch (err) {
      console.error(err);
    }
  }, [isFocused]);

  const [dateFrom, setDateFrom] = React.useState(new Date());
  const [dateFromError, setDateFromError] = React.useState('');
  const [dateTo, setDateTo] = React.useState(new Date());
  const [dateToError, setDateToError] = React.useState('');
  const [ioAction, setIoAction] = React.useState('');
  const [loading, setLoading] = React.useState(false);
  const [screenName, setScreenName] = React.useState('Vacaciones');

  return (
    <ScreenContainer scrollable={false} hasSafeArea={true}>
      <Utils.CustomCodeErrorBoundary>
        <CustomCode.DigicardScreenHeader
          {...props}
          right={{
            icon: 'Ionicons/home-outline',
            nav: 'Main',
            screen: 'InicioScreen',
          }}
          title={screenName}
        />
      </Utils.CustomCodeErrorBoundary>
      <CustomCode.LoadingOverlay
        {...props}
        visible={loading}
        action={ioAction}
      />

      <View style={styles.View39912261}>
        <Image
          style={styles.Image5ab8736d}
          resizeMode={'contain'}
          source={Images.UndrawAircraftFbvl}
        />
      </View>

      <View style={[styles.View3aa72707, { borderRadius: 12 }]}>
        <View
          style={[
            styles.View2fb13bc4,
            { backgroundColor: theme.colors.secondary },
          ]}
        >
          <Text
            style={[styles.Text577135e1, { color: theme.colors.background }]}
          >
            {'Período Solicitado'}
          </Text>
        </View>

        <View style={styles.Viewb373cbf7}>
          <View>
            {/* dpFrom */}
            <>
              {!!(props.route?.params?.readOnly ?? false) ? null : (
                <DatePicker
                  onDateChange={newDpFromValue => {
                    try {
                      setDateFrom(newDpFromValue);
                    } catch (err) {
                      console.error(err);
                    }
                  }}
                  style={styles.DatePicker9d0ad012}
                  label={'Desde'}
                  date={dateFrom}
                  mode={'date'}
                  leftIconMode={'inset'}
                  type={'solid'}
                  disabled={false}
                />
              )}
            </>
            {/* dpFromRO */}
            <>
              {!(props.route?.params?.readOnly ?? false) ? null : (
                <DatePicker
                  style={styles.DatePicker9d0ad012}
                  label={'Desde'}
                  date={dateFrom}
                  mode={'date'}
                  leftIconMode={'inset'}
                  type={'solid'}
                  disabled={true}
                />
              )}
            </>
            <>
              {!dateFromError ? null : (
                <Text
                  style={[styles.Text8099cd07, { color: theme.colors.error }]}
                  numberOfLines={1}
                >
                  {dateFromError}
                </Text>
              )}
            </>
          </View>

          <View>
            {/* dpTo */}
            <>
              {!!(props.route?.params?.readOnly ?? false) ? null : (
                <DatePicker
                  onDateChange={newDpToValue => {
                    try {
                      setDateTo(newDpToValue);
                    } catch (err) {
                      console.error(err);
                    }
                  }}
                  style={styles.DatePicker9d0ad012}
                  label={'Hasta'}
                  mode={'date'}
                  leftIconMode={'inset'}
                  type={'solid'}
                />
              )}
            </>
            {/* dpToRO */}
            <>
              {!(props.route?.params?.readOnly ?? false) ? null : (
                <DatePicker
                  style={styles.DatePicker9d0ad012}
                  label={'Hasta'}
                  date={dateTo}
                  mode={'date'}
                  leftIconMode={'inset'}
                  type={'solid'}
                  disabled={true}
                />
              )}
            </>
            <>
              {!dateToError ? null : (
                <Text
                  style={[styles.Text8099cd07, { color: theme.colors.error }]}
                  numberOfLines={1}
                >
                  {dateToError}
                </Text>
              )}
            </>
          </View>
        </View>
      </View>

      <View style={styles.Viewb24a0daa}>
        <>
          {!!(props.route?.params?.readOnly ?? false) ? null : (
            <ButtonSolid
              onPress={() => {
                const handler = async () => {
                  try {
                    const validateError = validate();
                    if (validateError) {
                      return;
                    }
                    setIoAction('saving');
                    setLoading(true);
                    const fetchResult = await dcSSRequestVacPOST.mutateAsync({
                      emp_id: Constants['my_emp_id'],
                      req_datetime: now(),
                      req_vac_from: Date2IsoDateString(dateFrom),
                      req_vac_to: Date2IsoDateString(dateTo),
                    });
                    setLoading(false);
                    const isError = postResultToast(fetchResult);
                  } catch (err) {
                    console.error(err);
                  }
                };
                handler();
              }}
              style={[
                styles.ButtonSolid2901e77d,
                {
                  backgroundColor: theme.colors.primary,
                  color: theme.colors.background,
                },
              ]}
              title={'Solicitar'}
            />
          )}
        </>
      </View>
    </ScreenContainer>
  );
};

const styles = StyleSheet.create({
  ButtonSolid2901e77d: {
    borderRadius: 10,
    fontFamily: 'System',
    fontWeight: '700',
    marginLeft: '6%',
    marginRight: '6%',
    minHeight: 48,
    textAlign: 'center',
  },
  DatePicker9d0ad012: {
    marginTop: 5,
  },
  Image5ab8736d: {
    height: 250,
    opacity: 0.75,
    width: '100%',
  },
  Text577135e1: {
    fontFamily: 'System',
    fontWeight: '500',
    fontSize: 18,
  },
  Text8099cd07: {
    fontSize: 10,
    marginBottom: 1,
    textAlign: 'center',
  },
  View2fb13bc4: {
    alignItems: 'center',
    height: 45,
    justifyContent: 'center',
    marginBottom: 5,
    marginTop: 5,
  },
  View39912261: {
    alignItems: 'center',
  },
  View3aa72707: {
    marginLeft: '3%',
    marginRight: '3%',
  },
  Viewb24a0daa: {
    justifyContent: 'center',
    marginLeft: '3%',
    marginRight: '3%',
    marginTop: 25,
  },
  Viewb373cbf7: {
    marginBottom: 5,
    marginLeft: '3%',
    marginRight: '3%',
    marginTop: 5,
  },
});

export default withTheme(SolicitudVacacionesScreen);
