import React from 'react';
import * as CustomCode from '../CustomCode.js';
import * as DcSSApi from '../apis/DcSSApi.js';
import * as GlobalVariables from '../config/GlobalVariableContext';
import Images from '../config/Images';
import * as Utils from '../utils';
import {
  ButtonSolid,
  DatePicker,
  Divider,
  IconButton,
  Picker,
  ScreenContainer,
  TextField,
  withTheme,
} from '@draftbit/ui';
import { useIsFocused } from '@react-navigation/native';
import { ActivityIndicator, Image, StyleSheet, Text, View } from 'react-native';
import { KeyboardAwareScrollView } from 'react-native-keyboard-aware-scroll-view';

const ActualizacionDeDatosScreen = props => {
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;
  const setGlobalVariableValue = GlobalVariables.useSetValue();

  const frame = value => {
    return `[${value}]`;
  };

  const postResultToast = fetchResult => {
    return CustomCode.PostResultToast(
      fetchResult,
      screenName,
      'back',
      '',
      2000,
      props
    );
  };

  const Date2UTCString = aDate => {
    console.log(
      `Date2UTCString(${aDate}), type: ${typeof aDate}, ISO: ${aDate.toISOString()}, Local: ${aDate.toLocaleDateString()}`
    );

    aDate.setHours(0);

    const UTCString = aDate.toISOString().split('T')[0] + 'T00:00:00.000Z';
    console.log(`UTCString: ${UTCString}`);
    return UTCString;
  };

  const UTCString2Date = aUtcString => {
    var date = new Date(),
      tzo = -date.getTimezoneOffset(),
      dif = tzo >= 0 ? '+' : '-',
      pad = function (num) {
        return (num < 10 ? '0' : '') + num;
      };

    console.log(`ParseLocalDate(${aUtcString}), type: ${typeof aUtcString}`);

    if (aUtcString) {
      const off =
        dif +
        pad(Math.floor(Math.abs(tzo) / 60)) +
        ':' +
        pad(Math.abs(tzo) % 60);

      const newUtcString = aUtcString.split('T')[0] + 'T00:00:00' + off;

      console.log(
        `newUtcString: ${newUtcString}, type: ${typeof newUtcString}`
      );

      return new Date(newUtcString);
    } else return new Date();
  };

  const dropdownToast = (type, message, timeout, wait) => {
    // 'info' | 'warn' | 'error' | 'success'
    DropDownHolder.getDropDown().alertWithType(
      type,
      screenName,
      message,
      null,
      timeout
    );
  };

  const initPhoto = aPhoto => {
    console.log(`photo length: ${aPhoto?.length}`);

    const MIN_PHOTO_LENGTH = 100;

    if (aPhoto?.length > MIN_PHOTO_LENGTH) {
      console.log(`Setting DB photo`);
      setNewPhoto(aPhoto);
    } else {
      console.log(`Setting Dummy photo`);
      setNewPhoto(CustomCode?.GlobalImages?.Nophoto);
    }
  };
  // to use a global variable
  const variables = CustomCode.useValues();
  // to update or modify the value of a global variable
  const setVariable = CustomCode.useSetValue();

  const DropDownHolder = CustomCode.DropDownHolder;

  // const Nophoto = Images.Nophoto;
  // ["Ninguno","Vehiculo","Moto","Bici","Publico"]

  // setVariable({ key: 'loading', value: true });
  const { theme } = props;

  const dcSSEmployeePATCH = DcSSApi.useEmployeePATCH();
  const {
    loading: screenLoading,
    data: screenData,
    error: screenError,
  } = DcSSApi.useEmployeeGET({ emp_id: Constants['my_emp_id'] });

  const isFocused = useIsFocused();
  React.useEffect(() => {
    try {
      if (!isFocused) {
        return;
      }
      if (false) {
        return;
      }
      setIoAction('loading');
      setGlobalVariableValue({
        key: 'loading',
        value: false,
      });
    } catch (err) {
      console.error(err);
    }
  }, [isFocused]);
  React.useEffect(() => {
    try {
      console.log(screenData?.emp_birth_date);
      setNewBirthDate(UTCString2Date(screenData?.emp_birth_date));
      console.log(frame(screenData?.emp_transport));
      setNewTransport(screenData?.emp_transport);
      setNewPhoto(screenData?.emp_photo);
      setGlobalVariableValue({
        key: 'loading',
        value: false,
      });
    } catch (err) {
      console.error(err);
    }
  }, [screenData]);
  const [Transports, setTransports] = React.useState([
    'Ninguno',
    'Vehiculo',
    'Moto',
    'Bici',
    'Publico',
  ]);
  const [ioAction, setIoAction] = React.useState("'loading'");
  const [loading, setLoading] = React.useState(false);
  const [newAddress, setNewAddress] = React.useState('');
  const [newBirthDate, setNewBirthDate] = React.useState(new Date());
  const [newContact, setNewContact] = React.useState('');
  const [newFirstName, setNewFirstName] = React.useState('');
  const [newHealth, setNewHealth] = React.useState('');
  const [newLastName, setNewLastName] = React.useState('');
  const [newMail, setNewMail] = React.useState('');
  const [newPhone, setNewPhone] = React.useState('');
  const [newPhoto, setNewPhoto] = React.useState('');
  const [newTransport, setNewTransport] = React.useState('');
  const [screenName, setScreenName] = React.useState('Actualización de Datos');
  if (screenLoading) {
    return (
      <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
        <ActivityIndicator />
      </View>
    );
  }

  if (screenError) {
    return null;
  }

  if (!screenData) {
    return null;
  }

  return (
    <ScreenContainer
      scrollable={false}
      hasSafeArea={true}
      hasTopSafeArea={true}
    >
      <Utils.CustomCodeErrorBoundary>
        <CustomCode.DigicardScreenHeader
          {...props}
          right={{
            icon: 'Ionicons/home-outline',
            nav: 'Main',
            screen: 'InicioScreen',
          }}
          title={screenName}
        />
      </Utils.CustomCodeErrorBoundary>
      <Utils.CustomCodeErrorBoundary>
        <CustomCode.LoadingOverlay
          {...props}
          visible={loading}
          action={ioAction}
        />
      </Utils.CustomCodeErrorBoundary>
      <KeyboardAwareScrollView
        contentContainerStyle={styles.KeyboardAwareScrollViewb72b58c0Content}
      >
        <View style={styles.View9c37cc47}>
          <View
            style={[
              styles.View6159eacf,
              { borderColor: theme.colors.medium, borderRadius: 10 },
            ]}
          >
            <>
              {!newPhoto ? null : (
                <Image
                  style={styles.Imageaa080101}
                  resizeMode={'cover'}
                  source={{ uri: `${newPhoto}` }}
                />
              )}
            </>
            <>
              {newPhoto ? null : (
                <Image
                  style={styles.Imageaa080101}
                  resizeMode={'cover'}
                  source={Images.Nophoto}
                />
              )}
            </>
          </View>
          <IconButton
            onPress={() => {
              const handler = async () => {
                try {
                  const snap = await Utils.openCamera({
                    allowsEditing: true,
                    quality: 0.2,
                  });
                  setNewPhoto(snap);
                } catch (err) {
                  console.error(err);
                }
              };
              handler();
            }}
            style={styles.IconButton26942535}
            icon={'AntDesign/camerao'}
            size={48}
          />
        </View>
        {/* LastNameInputView */}
        <View style={styles.Viewdf29e2e2}>
          <Divider
            style={styles.Dividera9877098}
            color={theme.colors.divider}
            height={1}
          />
          {/* lastName */}
          <TextField
            onChangeText={newLastNameValue => {
              try {
                setNewLastName(newLastNameValue);
              } catch (err) {
                console.error(err);
              }
            }}
            label={'Apellido'}
            type={'solid'}
            underlineColor={theme.colors.light}
            leftIconName={'MaterialCommunityIcons/playlist-edit'}
            leftIconMode={'outset'}
            defaultValue={screenData?.emp_last_name}
          />
        </View>
        {/* FirstNameInputView */}
        <View style={styles.Viewdf29e2e2}>
          <Divider
            style={styles.Dividera9877098}
            color={theme.colors.divider}
            height={1}
          />
          {/* firstName */}
          <TextField
            onChangeText={newFirstNameValue => {
              try {
                setNewFirstName(newFirstNameValue);
              } catch (err) {
                console.error(err);
              }
            }}
            label={'Nombre(s)'}
            type={'solid'}
            underlineColor={theme.colors.light}
            leftIconName={'MaterialCommunityIcons/playlist-edit'}
            leftIconMode={'outset'}
            defaultValue={screenData?.emp_first_name}
          />
        </View>
        {/* AddressInputView */}
        <View style={styles.Viewdf29e2e2}>
          <Divider
            style={styles.Dividera9877098}
            color={theme.colors.divider}
            height={1}
          />
          {/* address */}
          <TextField
            onChangeText={newAddressValue => {
              try {
                setNewAddress(newAddressValue);
              } catch (err) {
                console.error(err);
              }
            }}
            label={'Dirección'}
            type={'solid'}
            underlineColor={theme.colors.light}
            leftIconName={'FontAwesome/address-book-o'}
            leftIconMode={'outset'}
            defaultValue={screenData?.emp_address}
          />
        </View>
        {/* PhoneInputView */}
        <View style={styles.Viewdf29e2e2}>
          <Divider
            style={styles.Dividera9877098}
            height={1}
            color={theme.colors.divider}
          />
          {/* phoneNumber */}
          <TextField
            onChangeText={newPhoneNumberValue => {
              try {
                setNewPhone(newPhoneNumberValue);
              } catch (err) {
                console.error(err);
              }
            }}
            label={'Móvil'}
            type={'solid'}
            underlineColor={theme.colors.light}
            leftIconName={'Entypo/phone'}
            leftIconMode={'outset'}
            defaultValue={screenData?.emp_phone}
          />
        </View>
        {/* MailInputView */}
        <View style={styles.Viewdf29e2e2}>
          <Divider
            style={styles.Dividera9877098}
            color={theme.colors.divider}
            height={1}
          />
          {/* email */}
          <TextField
            onChangeText={newEmailValue => {
              try {
                setNewMail(newEmailValue);
              } catch (err) {
                console.error(err);
              }
            }}
            label={'Correo electrónico'}
            type={'solid'}
            underlineColor={theme.colors.light}
            leftIconMode={'outset'}
            leftIconName={'Entypo/email'}
            defaultValue={screenData?.emp_mail}
          />
        </View>
        {/* BirthdateInputView */}
        <View style={styles.Viewdf29e2e2}>
          <Divider
            style={styles.Dividera9877098}
            color={theme.colors.divider}
            height={1}
          />
          {/* birthday */}
          <DatePicker
            onDateChange={newBirthdayValue => {
              try {
                setNewBirthDate(newBirthdayValue);
              } catch (err) {
                console.error(err);
              }
            }}
            label={'Fecha Nacimiento'}
            date={newBirthDate}
            mode={'date'}
            leftIconMode={'outset'}
            type={'solid'}
            leftIconName={'MaterialIcons/date-range'}
            locale={'es-AR'}
          />
        </View>
        {/* ContactInputView */}
        <View style={styles.Viewdf29e2e2}>
          <Divider
            style={styles.Dividera9877098}
            color={theme.colors.divider}
            height={1}
          />
          {/* contact */}
          <TextField
            onChangeText={newContactValue => {
              try {
                setNewContact(newContactValue);
              } catch (err) {
                console.error(err);
              }
            }}
            label={'Contacto Emergencia'}
            type={'solid'}
            underlineColor={theme.colors.light}
            leftIconName={'AntDesign/medicinebox'}
            leftIconMode={'outset'}
            defaultValue={screenData?.emp_contact}
          />
        </View>
        {/* HealthInputView */}
        <View style={styles.Viewdf29e2e2}>
          <Divider
            style={styles.Dividera9877098}
            color={theme.colors.divider}
            height={1}
          />
          {/* healthPlan */}
          <TextField
            onChangeText={newHealthPlanValue => {
              try {
                setNewHealth(newHealthPlanValue);
              } catch (err) {
                console.error(err);
              }
            }}
            label={'Obra Social'}
            type={'solid'}
            underlineColor={theme.colors.light}
            leftIconName={'MaterialCommunityIcons/doctor'}
            leftIconMode={'outset'}
            defaultValue={screenData?.emp_health}
          />
        </View>
        {/* TransportInputView */}
        <View style={styles.Viewdf29e2e2}>
          <Divider
            style={styles.Dividera9877098}
            color={theme.colors.divider}
            height={1}
          />
          {/* transport */}
          <Picker
            onValueChange={newTransportValue => {
              const pickerValue = newTransportValue;
              try {
                setNewTransport(newTransportValue);
              } catch (err) {
                console.error(err);
              }
            }}
            style={{ borderColor: theme.colors.light }}
            label={'Transporte'}
            options={Transports}
            placeholder={'Seleccione medio de transporte'}
            leftIconMode={'outset'}
            type={'solid'}
            leftIconName={'Ionicons/car'}
            defaultValue={screenData?.emp_transport}
          />
        </View>
        {/* DocTypeInputView */}
        <View style={styles.Viewdf29e2e2}>
          <Divider
            style={styles.Dividera9877098}
            color={theme.colors.divider}
            height={1}
          />
          {/* docType */}
          <TextField
            label={'Tipo de documento'}
            type={'solid'}
            underlineColor={theme.colors.light}
            leftIconName={'MaterialCommunityIcons/playlist-edit'}
            leftIconMode={'outset'}
            disabled={true}
            defaultValue={screenData?.emp_doc_type}
          />
        </View>
        {/* DocNumberInputView */}
        <View style={styles.Viewdf29e2e2}>
          <Divider
            style={styles.Dividera9877098}
            color={theme.colors.divider}
            height={1}
          />
          {/* docNumber */}
          <TextField
            label={'Número de documento'}
            type={'solid'}
            underlineColor={theme.colors.light}
            leftIconName={'AntDesign/idcard'}
            leftIconMode={'outset'}
            disabled={true}
            defaultValue={screenData?.emp_doc_number}
          />
          <Divider
            style={styles.Dividera9877098}
            height={1}
            color={theme.colors.divider}
          />
        </View>
      </KeyboardAwareScrollView>

      <View style={styles.View6adc94d2}>
        <ButtonSolid
          onPress={() => {
            const handler = async () => {
              try {
                setIoAction('saving');
                setLoading(true);
                const fetchResult = await dcSSEmployeePATCH.mutateAsync({
                  address: newAddress,
                  birth_date: Date2UTCString(newBirthDate),
                  contact: newContact,
                  emp_id: Constants['my_emp_id'],
                  emp_photo: newPhoto,
                  first_name: newFirstName,
                  health: newHealth,
                  last_name: newLastName,
                  mail: newMail,
                  phone: newPhone,
                  transport: newTransport,
                });
                setLoading(false);
                postResultToast(fetchResult);
              } catch (err) {
                console.error(err);
              }
            };
            handler();
          }}
          style={[
            styles.ButtonSolidd936eb07,
            {
              backgroundColor: theme.colors.primary,
              color: theme.colors.background,
            },
          ]}
          title={'Actualizar'}
        />
      </View>
    </ScreenContainer>
  );
};

